a.btn.btn-large.btn-floating.socialMediaBtn {
    background: -webkit-gradient(linear, left top, right top, from(rgb(40, 40, 40)) , to(rgb(33, 60, 40)));
    background: -webkit-linear-gradient(left, rgb(40, 40, 40) , rgb(33, 60, 40));
    background: -o-linear-gradient(left, rgb(40, 40, 40) , rgb(33, 60, 40));
    background: linear-gradient(to right, rgb(40, 40, 40) , rgb(33, 60, 40));
    /* position: static; */
    bottom: 0;
    border-radius: none;
}
.SCicon {
    background: -webkit-gradient(linear, left top, right top, from(rgb(12, 7, 7)) , to(rgba(43, 32, 7, 0.707)));
    background: -webkit-linear-gradient(left, rgb(12, 7, 7) , rgba(43, 32, 7, 0.707));
    background: -o-linear-gradient(left, rgb(12, 7, 7) , rgba(43, 32, 7, 0.707));
    background: linear-gradient(to right, rgb(12, 7, 7) , rgba(43, 32, 7, 0.707));
    padding: 5px;
    color: rgb(255, 255, 255);
    font-size: 13px;
    border-left: 3px rgb(0, 0, 0) solid;
    border-bottom: 3px rgb(0, 0, 0) solid;
}
.VSCicon {
    background: -webkit-gradient(linear, left top, right top, from(rgb(12, 7, 7)) , to(rgba(43, 32, 7, 0.707)));
    background: -webkit-linear-gradient(left, rgb(12, 7, 7) , rgba(43, 32, 7, 0.707));
    background: -o-linear-gradient(left, rgb(12, 7, 7) , rgba(43, 32, 7, 0.707));
    background: linear-gradient(to right, rgb(12, 7, 7) , rgba(43, 32, 7, 0.707));
    padding: 0px 5px;
    color: rgb(255, 255, 255);
    font-size: 13px;
    border-bottom: 3px rgb(0, 0, 0) solid;
    position: relative;
    right: 40px;
}



